# Êô∫Ê†∏ÂèÇÊï∞ÁÆ°ÁêÜÈ°µÈù¢
<template>
  <div class="ai-parameter-page">
    <!-- È°µÈù¢Ê†áÈ¢ò -->
    <div class="page-header">
      <h1>Êô∫Ê†∏ÂèÇÊï∞ÁÆ°ÁêÜ</h1>
      <BaseButton type="primary" @click="handleAdd">Êñ∞Â¢ûÂèÇÊï∞</BaseButton>
    </div>

    <!-- ÊêúÁ¥¢Ê†è -->
    <div class="search-bar">
      <BaseInput 
        v-model="searchQuery.name" 
        placeholder="ÊêúÁ¥¢ÂèÇÊï∞ÂêçÁß∞"
        style="width: 300px"
      />
      <BaseSelect
        v-model="searchQuery.parameter_type_id"
        :options="parameterTypeOptions"
        placeholder="ÂèÇÊï∞Á±ªÂûã"
        style="width: 200px"
      />
      <BaseSelect
        v-model="searchQuery.rule_id"
        :options="ruleOptions"
        placeholder="Ê†∏‰øùËßÑÂàô"
        style="width: 200px"
      />
      <BaseButton @click="handleSearch">ÊêúÁ¥¢</BaseButton>
      <BaseButton @click="handleReset">ÈáçÁΩÆ</BaseButton>
    </div>

    <!-- Êï∞ÊçÆË°®Ê†º -->
    <div class="parameter-table">
      <!-- Âä†ËΩΩÁä∂ÊÄÅ -->
      <div v-if="loading" class="loading-wrapper">
        <div class="skeleton-row" v-for="i in 5" :key="i">
          <div class="skeleton-cell" v-for="j in 6" :key="j"></div>
        </div>
      </div>

      <!-- Á©∫Áä∂ÊÄÅ -->
      <div v-else-if="!tableData.length" class="empty-state">
        <span class="icon">üì≠</span>
        <p>ÊöÇÊó†Êï∞ÊçÆ</p>
      </div>

      <!-- Êï∞ÊçÆË°®Ê†º -->
      <table v-else>
        <thead>
          <tr>
            <th>ÂèÇÊï∞ÂêçÁß∞</th>
            <th>ÂèÇÊï∞Á±ªÂûã</th>
            <th>Ê†∏‰øùËßÑÂàô</th>
            <th>ÂèÇÊï∞ÂÄº</th>
            <th>Áä∂ÊÄÅ</th>
            <th>ÂàõÂª∫Êó∂Èó¥</th>
            <th>Êõ¥Êñ∞Êó∂Èó¥</th>
            <th>Êìç‰Ωú</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="item in tableData" :key="item.id">
            <td>{{ item.name }}</td>
            <td>{{ item.parameter_type_name }}</td>
            <td>{{ item.rule_name }}</td>
            <td>{{ item.value }}</td>
            <td>
              <span :class="['status-badge', item.status === 'enabled' ? 'ENABLED' : 'DISABLED']">
                {{ item.status === 'enabled' ? 'ÂêØÁî®' : 'Á¶ÅÁî®' }}
              </span>
            </td>
            <td>{{ item.created_at }}</td>
            <td>{{ item.updated_at }}</td>
            <td>
              <BaseButton 
                size="small"
                @click="handleEdit(item)"
              >ÁºñËæë</BaseButton>
              <BaseButton 
                type="danger" 
                size="small"
                @click="handleDelete(item)"
              >Âà†Èô§</BaseButton>
            </td>
          </tr>
        </tbody>
      </table>

      <!-- ÂàÜÈ°µ -->
      <div class="pagination">
        <BasePagination
          v-model:current="page"
          v-model:pageSize="pageSize"
          :total="total"
          @change="handlePageChange"
        />
      </div>
    </div>

    <!-- Êñ∞Â¢û/ÁºñËæëÂºπÁ™ó -->
    <BaseModal
      v-model="showModal"
      :title="modalType === 'add' ? 'Êñ∞Â¢ûÂèÇÊï∞' : 'ÁºñËæëÂèÇÊï∞'"
      width="500px"
      @confirm="handleModalConfirm"
    >
      <div class="form">
        <div class="form-item">
          <label>ÂèÇÊï∞ÂêçÁß∞</label>
          <BaseInput
            v-model="formData.name"
            placeholder="ËØ∑ËæìÂÖ•ÂèÇÊï∞ÂêçÁß∞"
            :maxlength="100"
          />
          <div class="form-error" v-if="formErrors.name">
            {{ formErrors.name }}
          </div>
        </div>
        
        <div class="form-item">
          <label>ÂèÇÊï∞Á±ªÂûã</label>
          <BaseSelect
            v-model="formData.parameter_type_id"
            :options="parameterTypeOptions"
            placeholder="ËØ∑ÈÄâÊã©ÂèÇÊï∞Á±ªÂûã"
          />
          <div class="form-error" v-if="formErrors.parameter_type_id">
            {{ formErrors.parameter_type_id }}
          </div>
        </div>

        <div class="form-item">
          <label>Ê†∏‰øùËßÑÂàô</label>
          <BaseSelect
            v-model="formData.rule_id"
            :options="ruleOptions"
            placeholder="ËØ∑ÈÄâÊã©Ê†∏‰øùËßÑÂàô"
          />
          <div class="form-error" v-if="formErrors.rule_id">
            {{ formErrors.rule_id }}
          </div>
        </div>

        <div class="form-item">
          <label>ÂèÇÊï∞ÂÄº</label>
          <BaseInput
            v-model="formData.value"
            type="textarea"
            placeholder="ËØ∑ËæìÂÖ•ÂèÇÊï∞ÂÄº"
            :maxlength="500"
            :rows="4"
          />
          <div class="form-error" v-if="formErrors.value">
            {{ formErrors.value }}
          </div>
        </div>

        <div class="form-item">
          <label>ÂèÇÊï∞ÊèèËø∞</label>
          <BaseInput
            v-model="formData.description"
            type="textarea"
            placeholder="ËØ∑ËæìÂÖ•ÂèÇÊï∞ÊèèËø∞"
            :maxlength="500"
            :rows="4"
          />
        </div>
      </div>
    </BaseModal>
  </div>
</template>

<script setup lang="ts">
import { ref, reactive, onMounted, computed, watch } from 'vue'
import { useRouter, useRoute } from 'vue-router'
import { useMessage } from '@/composables/useMessage'
import type { ApiResponse } from '@/types/api'
import type { Parameter, ParameterType } from '@/types/underwriting'
import {
  getParameterList,
  getParameterTypes,
  getRules,
  deleteParameter,
  createParameter,
  updateParameter
} from '@/api/underwriting'
import BaseButton from '@/components/base/BaseButton.vue'
import BaseInput from '@/components/base/BaseInput.vue'
import BaseSelect from '@/components/base/BaseSelect.vue'
import BasePagination from '@/components/base/BasePagination.vue'
import BaseModal from '@/components/base/BaseModal.vue'

// Áä∂ÊÄÅÊûö‰∏æ
enum ParameterStatus {
  ENABLED = 'enabled',
  DISABLED = 'disabled'
}

// Êü•ËØ¢ÂèÇÊï∞Á±ªÂûã
interface ParameterQuery {
  name: string
  parameter_type_id?: number
  rule_id?: number
  page: number
  pageSize: number
}

// ÂàÜÈ°µÂìçÂ∫îÁ±ªÂûã
interface PaginatedResponse<T> {
  list: T[]
  pagination: {
    total: number
    page: number
    pageSize: number
  }
}

const router = useRouter()
const route = useRoute()
const message = useMessage()

// Êü•ËØ¢ÂèÇÊï∞
const searchQuery = ref<ParameterQuery>({
  name: '',
  parameter_type_id: undefined,
  rule_id: undefined,
  page: 1,
  pageSize: 10
})

// Áä∂ÊÄÅÈÄâÈ°π
const statusOptions = [
  { value: ParameterStatus.ENABLED, label: 'ÂêØÁî®' },
  { value: ParameterStatus.DISABLED, label: 'Á¶ÅÁî®' }
]

// ÂàÜÈ°µÂèÇÊï∞
const page = ref(1)
const pageSize = ref(10)
const total = ref(0)

// Ë°®Ê†ºÊï∞ÊçÆ
const tableData = ref<Parameter[]>([])
const loading = ref(false)

// ‰∏ãÊãâÈÄâÈ°πÊï∞ÊçÆ
const parameterTypes = ref<ParameterType[]>([])
const rules = ref<{ id: number; name: string }[]>([])

// ‰∏ãÊãâÈÄâÈ°π
const parameterTypeOptions = computed(() => 
  parameterTypes.value.map(type => ({
    label: type.name,
    value: type.id
  }))
)

const ruleOptions = computed(() => 
  rules.value.map(rule => ({
    label: rule.name,
    value: rule.id
  }))
)

// Âä†ËΩΩË°®Ê†ºÊï∞ÊçÆ
const loadData = async () => {
  loading.value = true
  try {
    const res = await getParameterList({
      page: page.value,
      pageSize: pageSize.value,
      name: searchQuery.value.name || undefined,
      parameter_type_id: searchQuery.value.parameter_type_id,
      rule_id: searchQuery.value.rule_id
    })
    
    if (res.code === 200) {
      tableData.value = res.data.list || []
      total.value = res.data.total || 0
    } else {
      message.error(res.message || 'Âä†ËΩΩÂ§±Ë¥•')
      tableData.value = []
      total.value = 0
    }
  } catch (error: any) {
    console.error('Âä†ËΩΩÂèÇÊï∞ÂàóË°®Â§±Ë¥•:', error)
    message.error(error.message || 'Âä†ËΩΩÂ§±Ë¥•')
    tableData.value = []
    total.value = 0
  } finally {
    loading.value = false
  }
}

// Âä†ËΩΩÂèÇÊï∞Á±ªÂûãÂàóË°®
const loadParameterTypes = async () => {
  try {
    const res = await getParameterTypes()
    if (res.code === 200) {
      parameterTypes.value = res.data
    } else {
      message.error(res.message || 'Ëé∑ÂèñÂèÇÊï∞Á±ªÂûãÂ§±Ë¥•')
    }
  } catch (error: any) {
    console.error('Ëé∑ÂèñÂèÇÊï∞Á±ªÂûãÂ§±Ë¥•:', error)
    message.error(error.message || 'Ëé∑ÂèñÂèÇÊï∞Á±ªÂûãÂ§±Ë¥•')
  }
}

// Âä†ËΩΩËßÑÂàôÂàóË°®
const loadRules = async () => {
  try {
    const res = await getRules()
    if (res.code === 200) {
      rules.value = res.data
    } else {
      message.error(res.message || 'Ëé∑ÂèñËßÑÂàôÂàóË°®Â§±Ë¥•')
    }
  } catch (error: any) {
    console.error('Ëé∑ÂèñËßÑÂàôÂàóË°®Â§±Ë¥•:', error)
    message.error(error.message || 'Ëé∑ÂèñËßÑÂàôÂàóË°®Â§±Ë¥•')
  }
}

// ÊêúÁ¥¢
const handleSearch = () => {
  page.value = 1
  loadData()
}

// ÈáçÁΩÆ
const handleReset = () => {
  searchQuery.value.name = ''
  searchQuery.value.parameter_type_id = undefined
  searchQuery.value.rule_id = undefined
  page.value = 1
  loadData()
}

// ÂàÜÈ°µÂèòÂåñ
const handlePageChange = () => {
  loadData()
}

// ÂºπÁ™óÊéßÂà∂
const showModal = ref(false)
const modalType = ref<'add' | 'edit'>('add')
const currentId = ref<number>()

// Ë°®ÂçïÊï∞ÊçÆ
const formData = reactive({
  name: '',
  parameter_type_id: undefined as number | undefined,
  rule_id: undefined as number | undefined,
  value: '',
  description: undefined as string | undefined,
  status: ParameterStatus.ENABLED
})

// Ë°®ÂçïÈîôËØØ
const formErrors = reactive({
  name: '',
  parameter_type_id: '',
  rule_id: '',
  value: ''
})

// Ë°®ÂçïÈ™åËØÅ
const validateForm = () => {
  let valid = true
  formErrors.name = ''
  formErrors.parameter_type_id = ''
  formErrors.rule_id = ''
  formErrors.value = ''

  if (!formData.name) {
    formErrors.name = 'ËØ∑ËæìÂÖ•ÂèÇÊï∞ÂêçÁß∞'
    valid = false
  }

  if (!formData.parameter_type_id) {
    formErrors.parameter_type_id = 'ËØ∑ÈÄâÊã©ÂèÇÊï∞Á±ªÂûã'
    valid = false
  }

  if (!formData.rule_id) {
    formErrors.rule_id = 'ËØ∑ÈÄâÊã©Ê†∏‰øùËßÑÂàô'
    valid = false
  }

  if (!formData.value) {
    formErrors.value = 'ËØ∑ËæìÂÖ•ÂèÇÊï∞ÂÄº'
    valid = false
  }

  return valid
}

// Êñ∞Â¢û
const handleAdd = () => {
  modalType.value = 'add'
  currentId.value = undefined
  formData.name = ''
  formData.parameter_type_id = undefined
  formData.rule_id = undefined
  formData.value = ''
  formData.description = undefined
  formData.status = ParameterStatus.ENABLED
  showModal.value = true
}

// ÁºñËæë
const handleEdit = (item: Parameter) => {
  modalType.value = 'edit'
  currentId.value = item.id
  formData.name = item.name
  formData.parameter_type_id = item.parameter_type_id
  formData.rule_id = item.rule_id
  formData.value = item.value
  formData.description = item.description
  formData.status = item.status as ParameterStatus
  showModal.value = true
}

// Âà†Èô§
const handleDelete = async (item: Parameter) => {
  try {
    await message.confirm(`Á°ÆÂÆöË¶ÅÂà†Èô§ÂèÇÊï∞"${item.name}"ÂêóÔºü`)
    const res = await deleteParameter(item.id)
    if (res.code === 200) {
      message.success('Âà†Èô§ÊàêÂäü')
      loadData()
    } else {
      message.error(res.message || 'Âà†Èô§Â§±Ë¥•')
    }
  } catch (error: any) {
    console.error('Âà†Èô§Â§±Ë¥•:', error)
    message.error(error.message || 'Âà†Èô§Â§±Ë¥•')
  }
}

// ÂºπÁ™óÁ°ÆËÆ§
const handleModalConfirm = async () => {
  if (!validateForm()) return

  try {
    const requestData = {
      name: formData.name,
      parameter_type_id: formData.parameter_type_id,
      rule_id: formData.rule_id,
      value: formData.value,
      description: formData.description || '',
      status: formData.status
    }

    if (modalType.value === 'add') {
      const res = await createParameter(requestData)
      if (res.code === 200) {
        message.success('ÂàõÂª∫ÊàêÂäü')
        showModal.value = false
        loadData()
      } else {
        message.error(res.message || 'ÂàõÂª∫Â§±Ë¥•')
      }
    } else if (currentId.value) {
      const res = await updateParameter(currentId.value, requestData)
      if (res.code === 200) {
        message.success('Êõ¥Êñ∞ÊàêÂäü')
        showModal.value = false
        loadData()
      } else {
        message.error(res.message || 'Êõ¥Êñ∞Â§±Ë¥•')
      }
    }
  } catch (error: any) {
    console.error('‰øùÂ≠òÂ§±Ë¥•:', error)
    message.error(error.message || '‰øùÂ≠òÂ§±Ë¥•')
  }
}

// ÁõëÂê¨Ë∑ØÁî±ÂèÇÊï∞ÂèòÂåñ
watch(
  () => route.query.refresh,
  () => {
    if (route.query.refresh) {
      loadData()
    }
  }
)

onMounted(() => {
  loadParameterTypes()
  loadRules()
  loadData()
})
</script>

